import { Component } from '@angular/core';
import { NavController,Alert, AlertController,reorderArray } from 'ionic-angular';
import { ArchiveFriendPage}  from '../archive-friend/archive-friend'
import { FriendService } from '../../providers/friend-service/friend-service';
@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  
  public friendList=["Avnish","Ashish"];
  public reorderIsEnable=false;
  public archivePage=ArchiveFriendPage;
  constructor(private friendService:FriendService,public navCtrl: NavController,private alertController:AlertController) {
this.friendList=this.friendService.getFriend();
  }
  AvnishFriendAlert()
  {
   let addFriendAlert=this.alertController.create({
     title:"Add a friend",
     message:"Enter your friend name",
     inputs:[
       {
         type:"text",
         name:"addAvnishFriend"
       }
     ],
     buttons:[
       {
text:"Cancel"
       },
       {
          text:"Add Friend",
          handler:(inputData)=>{
                    let textdata;
                    textdata=inputData.addAvnishFriend;
                    this.friendService.addFriend(textdata);
}
       }
     ]
   });
   addFriendAlert.present()
  }
  toggleReorder()
  {
    this.reorderIsEnable=!this.reorderIsEnable;
  }
  ItemReorder($event){
reorderArray(this.friendList,$event);
  }
  gotoArchivePage(){
    this.navCtrl.push(ArchiveFriendPage);
  }
  archiveFriend(friendIndex){
    
    this.friendService.archivedFriend(friendIndex);

  }
}



