import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController } from 'ionic-angular';
import { FriendService } from '../../providers/friend-service/friend-service';

/**
 * Generated class for the ArchiveFriendPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-archive-friend',
  templateUrl: 'archive-friend.html',
})
export class ArchiveFriendPage {

  public archivedFriend=[];

  constructor(private friendService:FriendService,public navCtrl: NavController,private alertController:AlertController) {
  
      }


  ionViewDidLoad() {
    this.archivedFriend=this.friendService.getArchivedFriend();
  }

}



