import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the FriendServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FriendService {
private friends=[];
private archiveFriend=[];
  constructor(public http: HttpClient) {
    console.log('Hello FriendServiceProvider Provider');
  }
  archivedFriend(friendIndex){
  alert("hii");
    let friendToBeArchived=this.friends[friendIndex];
    this.friends.splice(friendIndex,1);
    this.archiveFriend.push(friendToBeArchived);

  }

  getFriend(){
    return this.friends;
  }
  getArchivedFriend(){
    return this.archiveFriend;
  }
  addFriend(friendText){
    this.friends.push(friendText);
  }


}



